.text-center {
    text-align: center;
}

.text-left {
    text-align: left;
}

.adjust {
    width: 100%;
}

.flex {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 2rem;
}

.flex-gap-grande {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 4rem;
}

.flex-simple {
    display: flex;
    align-items: center;
    gap: 0rem;
}

.flex-simple-center {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0rem;
}

.align-right {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 0rem;
}

.align-left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0rem;
}

.align-left-column {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 0rem;
}

.align-right-column {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 0rem;
}

.center-column {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.margin-bottom {
    margin-bottom: 2rem;
}

.margin-tiny-bottom {
    margin-bottom: 1rem;
}

.padding {
    padding: 3rem;
}

.margin-tiny-top {
    margin-top: 1rem;
}

.margin-top {
    margin-top: 2rem;
}

.margin-medium-top {
    margin-top: 4rem;
}

.big-margin-top {
    margin-top: 13rem;
}

.none {
    display: none;
}

.bold {
    font-weight: bold;
}

.margin-left {
    margin-left: 1.2rem;
}

.medium-margin-left {
    margin-left: 2rem;
}

.big-margin-left {
    margin-left: 12rem;
}

.margin-right {
    margin-right: 1.5rem;
}

.text-margin-0 {
    p {
        margin: 0 0 2rem 0;
    }
}