/** Media Queries **/
.alerta {
  border-radius: 0.5rem;
  padding: 0.75rem 1rem;
  margin-bottom: 1rem;
  font-size: 0.95rem;
  text-align: center;
}
.alerta.error {
  background-color: #ffeaea;
  color: #b00000;
  border: 1px solid #f5c2c2;
}

.cerrar-sesion {
  color: rgba(19, 54, 252, 0.7019607843);
  font-size: 1.4rem;
  margin-top: 0.5rem;
}
.cerrar-sesion:hover {
  font-style: italic;
  color: rgba(0, 0, 0, 0.8);
}

.naranja {
  color: rgb(255, 175, 101);
}

.azul {
  color: rgba(19, 54, 252, 0.7019607843);
}

.rojo {
  color: red;
}

.verde {
  color: rgb(22, 163, 33);
}

.hidden {
  display: none;
}

.oculto {
  display: none;
}

.disabled-select {
  background: #c4c4c4 !important;
  border-color: #ccc !important;
  cursor: not-allowed !important;
  color: #888 !important;
}

